<template>
  <section class="catalogs container">
    <TheTitle 
      :theme="theme"
      :title="title"
    />
    
    <!-- Catalogs Wrapper -->
    <div class="catalog-wrapper">
      <div
        v-if="catalogData.length"
        class="catalog-card" 
        v-for="data in catalogData" 
        :key="data._id"
      >
        <router-link :to="{name: 'Products', params: {category: data._id}}" class="link">
          <h2 class="name">{{ data.title }}</h2>
        </router-link>
      </div>
      <div class="no-connect" v-else>
        <h1>Loading...</h1>
      </div>
    </div>
  </section>
</template>

<script>
import TheTitle from '../components/The-title'
import axios from 'axios'

export default {
  name: 'Catalog',
  components: { TheTitle },
  data() {
    return {
      theme: 'Main menu / Catalog',
      title: 'Product catalog',
      catalogData: []
    } 
  },
  methods: {
    async getCategories() {
      const dataRes = await axios(`http://old.phoenixglobaltradings.com/categories`).then(res => res.data)
      this.catalogData = dataRes
      // console.log(dataRes)
    }
  },
  mounted() {
    this.getCategories()
  },
}
</script>

<style lang="scss">
@mixin media-size($size){
  @media(max-width: $size){
    @content
  }
}

.catalogs{
  min-height: 100vh;
  padding: 220px 0 70px;
  // Media
  @include media-size(1230px) {
    padding: 170px 0 30px;
  }
  @include media-size(790px) {
    padding: 150px 0 30px;
  }
  @include media-size(570px) {
    padding: 150px 15px 30px;
  }
  
  .catalog-wrapper{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px 50px;
    // Media
    @include media-size(1700px){
      gap: 30px;
    }
    
    .no-connect{
      h1{
        font-size: 80px;
        font-weight: 300;
        color: #999;
        // Media
        @include media-size(1200px){
          font-size: 50px;
        }
        @include media-size(570px){
          font-size: 30px;
        }
      }
    }
    
    
    .catalog-card{
      width: 380px;
      min-height: 150px;
      padding: 20px;
      background: #ffffff;
      box-shadow: -10px 50px 70px rgba(171, 175, 199, 0.4);
      // border-bottom: 5px solid #1d68bf;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      
      &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(to right top, #eb2027, #ba5400, #f7cf24);
        left: 0;
        top: 0;
        transform: translateY(95%);
        transition: 0.5s;
      }
      
      &:hover::before{
        transform: translateY(0);
      }
      
      &:hover .name{
        color: #ffffff;
      }
      
      .link{
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 3;
      }
      
      .name{
        font-size: 25px;
        color: #555555;
        position: relative;
        z-index: 2;
        transition: color 0.5s;
      }
    }
  }
}

</style>